nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  background-color: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.nav-logo {
  border-radius: 50%;
}

ul.nav-items {
  display: flex;
  gap: 1rem;
  list-style: none;
  margin: 0;
  padding: 0;
}

ul.nav-items li {
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #555555;
  transition: all 0.3s ease-in-out;
}

ul.nav-items li:hover {
  color: #ff0000;
  transform: translateY(-2px);
}

.main-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}

h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
  color: #333333;
}

ol {
  font-size: 1.2rem;
  color: #666666;
  text-align: left;
}

li {
  margin-bottom: 1rem;
}

.footer {
  background-color: #f2f2f2;
  padding: 1rem;
  text-align: center;
  color: #666666;
}
